html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif,思源黑體;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
*,*:before,*:after{
    box-sizing: border-box;
}
img{
vertical-align: bottom;
max-width:100%;
height:auto;
}


header{
  background-color:#AA0601 ;
}

.sub{
  max-width: 1296px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.logo {
  width: 80px;
  height: 48.75px;
  margin: 16px 0 15.43px 0;
}

.list{
  display: flex;
  padding: 25px 0;
  justify-content: center;
}

.pr-64{
  padding-right: 64px;
}

.list li a{
  color: white;
  text-decoration:none;
  font-size: 20px;
}

.list li a:hover{
  transition: all 1s;
  font-size: 26px;
  font-weight: bold;
  color:#000000;
}

footer{
  background-color:#AA0601 ;
}
.ft {
  width: 1296px;
  margin: 0 auto;
}
.footer-list{
  display: flex;
}
.footer-list li{
  padding:60px 48px 50px 0px;

}
.footer-list li a{
  color: white;
  text-decoration: none;
}
.footer-list li a:hover{
  transition: all 1s;
  font-weight: bold;
  color:#000000;
}

.contact{
  padding-bottom: 32px;
}
.contact img{
  width: 36px;
  height: 36px;
  
}
.contact li img{
 margin-right: 22px;
}
.contact li a{
  color: white;
  font-size: 36px;
  line-height: 54px;
  text-decoration: none;
}
.contact li a:hover{
  color:rgb(12, 33, 199);
}
.txt{
  display: flex;
  border-bottom:1px solid white ;
  flex-wrap: wrap;
}
.icon{
  display: flex;
  margin-top:48px;
  margin-left:575px ;
}
.icon li img:hover{
  transition: all 1s;
  transform: scale(1.5);
  
}
.mr-32{
  margin-right: 32px;
}
.regsiter{
  display: flex;
  color:white;
  justify-content: space-between;
  padding: 32px 0;
}
.regsiter p{
  font-size: 16px;
  line-height: 24px;
  
}
.mr-48{
  margin-right: 48px;
}
.regsiter ul {
  display: flex;
}
.regsiter ul li a{
