}

@media screen and (max-width:992px){
  header{
    width: 100%;
  }
  .sub{
    display: block;
    padding: 0;
  }
  .logo{
    margin: 0 auto;
    line-height: 88px;
    margin-bottom: 15.43px;
    img{
      vertical-align: middle;
    }
  }
  .list{
    flex-wrap: wrap;
  }
  .list li{
    width: 50%;
    text-align: center;
    border-top: 1px solid #FFF;
    border-right: 1px solid #FFF;
  }
  .list li a{
    display: block;
    margin: 0;
    padding: 12px 0;
    font-size: 16px;
  }
  .four{
    padding-right: 64px;
  }
  footer{
    width: 100%;
     display: block;
     flex-wrap: wrap;
  }
}
